import { StorageService } from './../../../services/storage.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';


import { PopupModalModule } from 'app/shared/modal/popup-modal.module';
import { CategoriasService } from './categoria.service';
import { SharedComponentModule } from './../../shared/shared-component.module';
import { CategoriasRoutingModule } from './categorias-routing.module';

import { ListarCategoriasComponent } from './listar/listar-categorias.component';
import { CreateCatetoriasComponent } from './create/create-catetorias.component';
import { EditCatetoriasComponent } from './edit/edit-catetorias.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    CategoriasRoutingModule

  ],
  declarations: [
    ListarCategoriasComponent,
    CreateCatetoriasComponent,
    EditCatetoriasComponent
  ],
  providers:[
    CategoriasService,
    StorageService
  ]
})
export class CategoriasModule { }



