import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';


import { CreateCatetoriasComponent } from './create/create-catetorias.component';
import { ListarCategoriasComponent } from './listar/listar-categorias.component';
import { EditCatetoriasComponent }   from './edit/edit-catetorias.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Categorias'
    },
    children: [
      {
        path: 'listar',
        component: ListarCategoriasComponent,
        data: {
          title: 'Listar'
        }
      },
      {
        path: 'novo',
        component: CreateCatetoriasComponent,
        data: {
          title: 'Nova categoria'
        }
      },
      {
        path: 'editar/:id',
        component: EditCatetoriasComponent,
        data: {
          title: 'Alterar categoria'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CategoriasRoutingModule { }



