import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { Component, OnInit, ViewChild } from '@angular/core';
import { API_CONFIG } from 'config/core/api.config';
import { CategoriaDTO } from 'models/categoria.dto';
import { CategoriasService } from '../categoria.service';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { Router } from '@angular/router';

@Component({
  selector: 'listar-categorias',
  templateUrl: './listar-categorias.component.html',
  styleUrls: ['./listar-categorias.component.scss']
})
export class ListarCategoriasComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('ExcluirCategoriaModal') popupExclusao;
  @ViewChild('ChangeSatusCategoriaModal') popupChangeStatus;
  bucketUrl: string = API_CONFIG.bucketBaseUrl;
  categoria: CategoriaDTO;


  items: CategoriaDTO[];

  constructor(public CategoriasService: CategoriasService, public router: Router) {
    super()
  }

  ngOnInit() {
    this.carregarCategorias();
  }

  selecionarItem(item: CategoriaDTO) {
    this.categoria = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: CategoriaDTO) {
    this.categoria = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.CategoriasService.changeStatus(this.categoria)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Status da categoria alterado com sucesso!!!')
        this.carregarCategorias();
      }, error => { });
  }

  excluirCategoria() {
    this.CategoriasService.excluir(this.categoria)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria excluída com sucesso!!!')
        this.carregarCategorias();
      }, error => { });
  }

  alterarCategoria(categoria: CategoriaDTO) {
    this.router.navigate([`categorias/editar/${categoria.id}`])
  }

  private carregarCategorias() {
    this.CategoriasService.findAll().subscribe(response => this.items = response, error => { });
  }
}



